import cartJs from './CartComp'
import productsJs from './ProductComp'
import basketJs from './BasketComp'
import catalogJs from './CatalogComp'
import footerJs from './FooterComp'
import goodJs from './GoodComp'
import headerJs from './HeaderComp'
import navJs from './NavComp'

const app = {
    el: '#app',
    components: {
        cartJs,
        productsJs,
        basketJs,
        catalogJs,
        footerJs,
        goodJs,
        headerJs,
        navJs,
    },
    methods: {
        getJson(url){ //         
            return fetch(url)
                .then(result => result.json())
                .catch(error => {
                    console.error(error);
                })
        },
        postJson(url, data) { //     
            return fetch(url, {
                method: 'POST',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data)
            }).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        },
        putJson(url, data) { //      1
            return fetch(url, {
                method: 'PUT',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data)
            }).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        },
        delJson(url, data) { //      1  ,    
            return fetch(url, {
                method: 'DELETE',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data)
            }).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        },
        changeJson(url, data) { //        
            return fetch(url, {
                method: 'CHECKOUT',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(data)
            }).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        },
        clearJson(url) { //   
            return fetch(url, {
                method: 'PURGE',
                headers: {
                    "Content-Type": "application/json"
                },
            }).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        },
        openJson(url) { //    
            return fetch(url).then(result => result.json())
                .catch(error => {
                    console.error(error);
                });
        }
    },
    mounted() {
        console.log(this);
    }
};

export default app;

